footer {
    background-color: #f8f8f8;
    color: #333;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding: 1rem;
    font-size: 1.2rem;
    text-align: center;
    border-bottom-left-radius: 10px;
    border-bottom-right-radius: 10px;
  }
  
  .icons {
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 1rem;
  }
  
  .icons a {
    margin: 0 0.8rem;
    color: #333;
    font-size: 1.8rem;
    transition: color 0.3s ease-in-out;
  }
  
  .icons a:hover {
    color: #0077b5;
  }
  
  @media only screen and (max-width: 600px) {
    .footer {
      font-size: 1rem;
    }
  
    .icons {
      margin-top: 0.5rem;
    }
  
    .icons a {
      margin: 0 0.5rem;
      font-size: 1.2rem;
    }
  }
  
  @media only screen and (min-width: 601px) {
    .footer {
      font-size: 1.2rem;
    }
  
    .icons {
      margin-top: 1rem;
    }
  
    .icons a {
      margin: 0 0.8rem;
      font-size: 1.8rem;
    }
  }